
import { COPYPASTEBTNS } from '../../constance';
import { rowCopyPasteUtils } from '../../tool/cardTableTools/rowCopyPasteUtils';

export default function(props, moduleId) {
        this.copyLineDatas[moduleId] = rowCopyPasteUtils.copyRows.call(
                this,
                props,
                moduleId,
                COPYPASTEBTNS[moduleId].initBtns,
                COPYPASTEBTNS[moduleId].pasteBtns
        );
}
