
import { UISTATUS } from '../../constance';
import { buttonControl } from '../viewControl/buttonControl';
import { clearCardData, mainOrgInit } from '../utils/cardPageUtil';

export default function(props) {
        props.setUrlParam({ status: UISTATUS.add });
        clearCardData.call(this, props);
        buttonControl.call(this, props);
        mainOrgInit.call(this, props, this.contexts);
}
