import { toast, promptBox } from 'nc-lightapp-front';
import { AREA } from '../../constance';

export function uploadAfterEvent(info) {
    if (info.file.status === 'done') {
        if (info.file.response.data) {
            let data = info.file.response.data.data;
            let infos = this.props.cardTable.getAllData(AREA.cardBody_1);
            if (data.bodys[AREA.cardBody_1]) {
                let impdata = new Map();
                //待导入数据
                data.bodys.cardBody_1.rows.forEach(item => {
                    let key = item.values.msyscode.value + item.values.mpk.value + item.values.mcode.value + item.values.mname.value;
                    impdata.set(key, item);
                });

                let newrows = [];
                //过滤界面上数据，补齐还是尾部追加
                infos.rows.forEach(item => {
                    //目的档案为空的
                    if (!item.values.hpk.value) {
                        //根据来源系统+PK+编码+名称 拼主键
                        let key = item.values.msyscode.value + item.values.mpk.value + item.values.mcode.value + item.values.mname.value;
                        let improw = impdata.get(key);
                        if (improw) {
                            item.values.hpk = improw.values.hpk;
                            item.values.hcode = improw.values.hcode;
                            item.values.hname = improw.values.hname;
                            item.values.hname1 = { value: improw.values.hcode.value, display: improw.values.hcode.value };
                            Object.keys(improw.values).forEach(fieldName => {
                                if (fieldName.indexOf('vdef') > -1) {
                                    if (item.values[fieldName].value == undefined || item.values[fieldName].value == '') {
                                        item.values[fieldName] = { value: improw.values[fieldName].value, display: improw.values[fieldName].value };
                                    }
                                }
                            });
                            newrows.push(item);
                            impdata.delete(key);
                        } else {
                            newrows.push(item);
                        }
                    } else {
                        newrows.push(item);
                    }
                });

                data.bodys[AREA.cardBody_1].rows = newrows.concat([...impdata.values()]);
                this.props.cardTable.setTableData(AREA.cardBody_1, data.bodys[AREA.cardBody_1]);
            }
            promptBox({
                color: 'success',
                title: "导入完成",
                content: info.file.response.data.externalData.impmsg,
                noCancelBtn: true,
            });
        }
        if (info.file.response.error) {
            toast({ color: 'warning', content: info.file.response.error.message });
        }
    }
}
