import { ajax, promptBox, toast, getBusinessInfo, formDownload } from 'nc-lightapp-front';
import { AREA, URL, PAGECODE, CARDTABLEAREAIDS, IVo2VoItf } from '../../constance';

export default function (props) {

    // 表头必输项校验
    let flag = props.form.isCheckNow(AREA.cardFormId);
    if (!flag) return;

    let headcols = props.meta.getMeta()[AREA.cardFormId].items.map(item => [item.attrcode, item.label]);
    let showhideflag = props.form.getFormItemsVisible(AREA.cardFormId, headcols.map(item => item[0]));
    let headdata = new Map();
    //获取表头显示的字段以及值
    headcols.forEach((item, index) => {
        if (showhideflag[index]) {
            headdata.set(item, props.form.getFormItemsValue(AREA.cardFormId, item[0]));
            // if (item[0] == 'srcbillitempk') {
            //     headdata.set(['srcbillitemtxt', '单据项目编码'], props.form.getFormItemsValue(AREA.cardFormId, 'srcbillitemtxt'));
            // }
        }
    });
    //获取表体显示的字段信息
    let bodydata = props.meta.getMeta()[AREA.cardBody_1].items.filter(item => item.visible && item.attrcode !== 'rowno' && item.attrcode !== 'opr').map(item => [item.attrcode, item.label]);

    formDownload({
        params: { headdata, bodydata },
        url: URL.editExport,
        enctype: 2
    });
}
