import { FIELDS, BUTTONID, AREA, URL, IVo2VoItf } from '../../constance';
import { ajax, getBusinessInfo } from 'nc-lightapp-front';
import * as MessageDialog from '../../tool/messageUtil';

export default function (props, moduleId, key, value, oldValue) {

    if (key === FIELDS.pk_org) {

        if (value.value) {
            props.resMetaAfterPkorgEdit(); //选择主组织以后，恢复其他字段的编辑性
            props.button.setDisabled({
                [BUTTONID.cardBody_1_AddLine]: false
            });
            let irows = props.cardTable.getNumberOfRows(AREA.cardBody_1);
            if (irows > 0) {
                MessageDialog.showConfirmDialog("提示", "将清空表头及表体所有行？", {
                    beSureBtnClick: () => {
                        props.form.setFormItemsValue(moduleId, {
                            'pk_mid_ref': { value: null, display: null },
                            'pk_hrp_ref': { value: null, display: null },
                            'idissys': { value: null, display: null },
                            'srcbilltype': { value: null, display: null },
                            'srcbillitempk': { value: null, display: null },
                        });
                        for (let i = irows; i >= 0; i--) {
                            props.cardTable.delRowsByIndex(AREA.cardBody_1, i);
                        }
                    },
                    cancelBtnClick: () => {
                        props.form.setFormItemsValue(moduleId, { 'pk_org': { value: oldValue.value, display: oldValue.display } });
                    }
                });
            } else {//没有表体数据的时候，直接清空 目标系统单据类型
                props.form.setFormItemsValue(moduleId, {
                    'pk_mid_ref': { value: null, display: null },
                    'pk_hrp_ref': { value: null, display: null },
                    'idissys': { value: null, display: null },
                    'srcbilltype': { value: null, display: null },
                    'srcbillitempk': { value: null, display: null },
                });
            }
        } else {
            props.initMetaByPkorg();
            props.button.setDisabled({
                [BUTTONID.cardBody_1_AddLine]: true
            });
            let irows = props.cardTable.getNumberOfRows(AREA.cardBody_1);
            props.form.setFormItemsValue(moduleId, {
                'pk_mid_ref': { value: null, display: null },
                'pk_hrp_ref': { value: null, display: null },
                'idissys': { value: null, display: null },
                'srcbilltype': { value: null, display: null },
                'srcbillitempk': { value: null, display: null },
            });
            for (let i = irows; i >= 0; i--) {
                props.cardTable.delRowsByIndex(AREA.cardBody_1, i);
            }
        }
    }
    //保存hrp——ref的旧值，以免清空后还原旧值找不到
    let pk_hrp_ref_old = props.form.getFormItemsValue(moduleId, 'pk_hrp_ref');

    cleanPk_hrp_refItem.call(this, props, moduleId, key, value, oldValue);

    if (key == FIELDS.pk_mid_ref) {
        let pk_mid_ref = props.form.getFormItemsValue(moduleId, 'pk_mid_ref').display;
        let idissys = props.form.getFormItemsValue(moduleId, 'idissys').value;
        if (pk_mid_ref == '来源科室档案' && (idissys == '1'|| idissys == '8')) {
            props.form.setFormItemsVisible(AREA.cardFormId, {
                'srcbilltype': true,
                'srcbillitempk': true,
                //'srcbillitemtxt': true
            });
        } else {
            props.form.setFormItemsVisible(AREA.cardFormId, {
                'srcbilltype': false,
                'srcbillitempk': false,
                'srcbillitemtxt': false
            });
        }

        pk_mid_refAfterEdit.call(this, props, moduleId, oldValue);
    }

    if (key == FIELDS.idissys) {//目标系统
        let idissys = props.form.getFormItemsValue(moduleId, 'idissys').value;
        //if(idissys == '1' || idissys == '8'){  add by mzq目的系统为成本时先不显示
        //         if(idissys == '1'){
        //                 props.form.setFormItemsVisible(AREA.cardFormId,  {
        //                         'srcbilltype': true,
        //                         'srcbillitempk': true,
        //                         //'srcbillitemtxt': true
        //                     });
        //        }else{
        //                 props.form.setFormItemsVisible(AREA.cardFormId,  {
        //                         'srcbilltype': false,
        //                         'srcbillitempk': false,
        //                         'srcbillitemtxt': false
        //                 });
        //        }

        let irows = props.cardTable.getNumberOfRows(AREA.cardBody_1);
        if (irows > 0) {
            MessageDialog.showConfirmDialog("提示", "将清空来源档案类型及表体所有行？", {
                beSureBtnClick: () => {
                    props.form.setFormItemsValue(moduleId, {
                        'pk_mid_ref': { value: null, display: null },
                        'pk_hrp_ref': { value: null, display: null },
                        'srcbilltype': { value: null, display: null },
                        'srcbillitempk': { value: null, display: null },
                    });
                    for (let i = irows; i >= 0; i--) {
                        props.cardTable.delRowsByIndex(AREA.cardBody_1, i);
                    }
                },
                cancelBtnClick: () => {
                    props.form.setFormItemsValue(moduleId, { 'idissys': { value: oldValue.value, display: oldValue.display } });
                }
            });
        } else {//没有表体数据的时候，直接清空 目标系统单据类型
            props.form.setFormItemsValue(moduleId, {
                'pk_mid_ref': { value: null, display: null },
                'srcbilltype': { value: null, display: null },
                'srcbillitempk': { value: null, display: null },
            });

        }
        //add by mzq查询动态列
        let srcbillitempk = props.form.getFormItemsValue(moduleId, 'srcbillitempk').value;
        srcbillitempkAfterEvent.call(this, props, moduleId, srcbillitempk, oldValue);
    }
    if (key == "pk_hrp_ref") {// 目的类型档案
        clean_hrp_refAfterEdit.call(this, props, moduleId, oldValue);
    }

    if (key == "srcbilltype") {

        let irows = props.cardTable.getNumberOfRows(AREA.cardBody_1);
        let srcbillitempk = props.form.getFormItemsValue(moduleId, 'srcbillitempk');
        let srcbillitemtxt = props.form.getFormItemsValue(moduleId, 'srcbillitemtxt');
        if (irows > 0) {
            MessageDialog.showConfirmDialog("提示", "根据来源单据类型配置指定字段对照关系，将清空单据项目及表体所有行？", {
                beSureBtnClick: () => {
                    props.form.setFormItemsValue(moduleId, {
                        'srcbillitempk': { value: null, display: null },
                    });
                    for (let i = irows; i >= 0; i--) {
                        props.cardTable.delRowsByIndex(AREA.cardBody_1, i);
                    }
                },
                cancelBtnClick: () => {
                    props.form.setFormItemsValue(moduleId, {
                        'srcbilltype': { value: oldValue.value, display: oldValue.display },
                        srcbillitempk,
                        srcbillitemtxt
                    }
                    );
                }
            });
            let meta = this.props.meta.getMeta();
            meta[AREA.cardBody_1].items.forEach(item => {
                // add by mzq 隐藏动态列

                if (item.attrcode.slice(0, 4) == "vdef") {
                    item.visible = false;
                }

            });
            this.props.meta.setMeta(meta);
            props.form.setFormItemsValue(moduleId, {
                'srcbillitempk': { value: null, display: null },
            });
        } else {
            let meta = this.props.meta.getMeta();
            meta[AREA.cardBody_1].items.forEach(item => {
                // add by mzq 隐藏动态列

                if (item.attrcode.slice(0, 4) == "vdef") {
                    item.visible = false;
                }

            });
            this.props.meta.setMeta(meta);
            props.form.setFormItemsValue(moduleId, {
                'srcbillitempk': { value: null, display: null },
            });
        }


    }
    if (key == "srcbillitempk") {
        billitempkAfterEdit.call(this, props, moduleId, value, oldValue);
    }

    let [{ value: pk_org }, { value: idissys }, { value: pk_mid_ref }, { value: pk_hrp_ref }] = props.form.getFormItemsValue('card_head', ['pk_org', 'idissys', 'pk_mid_ref', 'pk_hrp_ref']);
    if (pk_org && idissys && pk_mid_ref && pk_hrp_ref) {
        props.button.setButtonDisabled(BUTTONID.EditImport, false);
    } else {
        props.button.setButtonDisabled(BUTTONID.EditImport, true);
    }

}


function srcbillitempkAfterEvent(props, moduleId, value, oldValue) {
    let param = {};
    //add by mzq查询动态列
    param = {
        idissys: props.form.getFormItemsValue(moduleId, 'idissys').value,
        srcbilltyperefcode: props.form.getFormItemsValue(moduleId, 'srcbilltype').refcode,
        srcbillitempk: value == null ? null : value.value
    };
    let meta = this.props.meta.getMeta();
    ajax({
        url: URL.vdefcol,
        data: param,
        async: false,
        success: (res) => {
            let { success, data } = res;
            //填充数据
            if (success) {
                if (data.hidecols) {
                    props.cardTable.hideColByKey(AREA.cardBody_1, data.hidecols);
                }
                if (data.showlist) {
                    let map = new Map();
                    let fieldcodes = data.showlist.map(item => {
                        map.set(item.fieldcode, item.attrcode);
                        return item.fieldcode;
                    });
                    meta = this.props.meta.getMeta();
                    meta[AREA.cardBody_1].items.forEach(item => {
                        if (fieldcodes.indexOf(item.attrcode) > -1) {
                            item.attrname = data.namemap[map.get(item.attrcode)];
                            item.label = data.namemap[map.get(item.attrcode)];
                            //add by mzq业务类型下拉框
                            if (item.attrname === "业务类型") {
                                item.itemtype = "select";
                                let selectoptions = [{ display: '门诊', value: '1' }, { display: '住院', value: '2' }];
                                item.options = selectoptions;
                            }
                        }
                        // add by mzq 隐藏动态列
                        if (fieldcodes.length == 0) {
                            if (item.attrcode.slice(0, 4) == "vdef") {
                                item.visible = false;
                            }
                        }
                    });
                    this.props.meta.setMeta(meta);

                    props.cardTable.showColByKey(AREA.cardBody_1, fieldcodes);
                } else {
                    meta[AREA.cardBody_1].items.forEach(item => {
                        // add by mzq 隐藏动态列

                        if (item.attrcode.slice(0, 4) == "vdef") {
                            item.visible = false;
                        }

                    });
                    this.props.meta.setMeta(meta);
                }
                if (data.srcbillitemtxt) {
                    props.form.setFormItemsValue(moduleId, { 'srcbillitemtxt': { value: data.srcbillitemtxt, display: data.srcbillitemtxt } });
                } else {
                    props.form.setFormItemsValue(moduleId, { 'srcbillitemtxt': { value: null, display: null } });
                }

            }
        }
    });
}

function clean_hrp_refAfterEdit(props, moduleId, oldValue) {
    let irows = props.cardTable.getNumberOfRows(AREA.cardBody_1);
    if (irows != 0) {
        for (let i = 0; i < irows; i++) {
            props.cardTable.setValByKeyAndIndex(AREA.cardBody_1, i, 'hcode', { value: null, display: null });
            props.cardTable.setValByKeyAndIndex(AREA.cardBody_1, i, 'hname', { value: null, display: null });
            props.cardTable.setValByKeyAndIndex(AREA.cardBody_1, i, 'hname1', { value: null, display: null });
        }
    }
}

function pk_mid_refAfterEdit(props, moduleId, oldValue) {
    let tableId = AREA.cardBody_1;
    let [pk_org, pk_interdocref, idissys, pk_mid_ref, srcbilltype, srcbillitempk] = props.form.getFormItemsValue(moduleId, ["pk_org", "pk_interdocref", "idissys", "pk_mid_ref", "srcbilltype", "srcbillitempk"]);//目标系统
    if (pk_mid_ref.value) {
        //1、清空表体数据
        // props.cardTable.setStatus(tableId, 'browse');
        let irows = props.cardTable.getNumberOfRows(tableId);

        //2、根据不同参照设置不同的值
        let param = {
            pk_org: pk_org.value,
            pk_group: getBusinessInfo().groupId,
            idissys: idissys.value,
            midpk: pk_mid_ref.value,
            midname: pk_mid_ref.display,
            srcbilltyperefcode: srcbilltype.value,
            srcbillitempk: srcbillitempk.value,
        };
        if (pk_interdocref.value) {
            param.pk_interdocref = pk_interdocref.value;
        }

        if (irows > 0) {
            MessageDialog.showConfirmDialog("提示", "将清空表体所有行,是否继续？", {
                beSureBtnClick: () => {
                    props.form.setFormItemsValue(moduleId, { 'pk_hrp_ref': { value: null, display: null } });
                    for (let i = irows; i >= 0; i--) {
                        props.cardTable.delRowsByIndex(tableId, i);
                    }

                    addLine.call(this, props, param);
                },
                cancelBtnClick: () => {
                    props.form.setFormItemsValue(moduleId, {
                        'pk_mid_ref': { value: oldValue.value, display: oldValue.display }
                    });
                    let pk_mid_ref = props.form.getFormItemsValue(moduleId, 'pk_mid_ref').display;
                    let idissys = props.form.getFormItemsValue(moduleId, 'idissys').value;
                    if (pk_mid_ref == '来源科室档案' && (idissys == '1'|| idissys == '8')) {
                        props.form.setFormItemsVisible(AREA.cardFormId, {
                            'srcbilltype': true,
                            'srcbillitempk': true,
                            //'srcbillitemtxt': true
                        });
                        props.form.setFormItemsValue(AREA.cardFormId, {
                            srcbilltype,
                            srcbillitempk

                        });
                        props.form.setFormItemsDisabled(AREA.cardFormId, {
                            'srcbilltype': false,
                            'srcbillitempk': false,
                            'srcbillitemtxt': false
                        });
                    } else {
                        props.form.setFormItemsVisible(AREA.cardFormId, {
                            'srcbilltype': false,
                            'srcbillitempk': false,
                            'srcbillitemtxt': false
                        });
                    }
                }
            });
        } else {
            addLine.call(this, props, param);
        }

        //3、来源档案类型选择非来源科室档案时，来源单据和单据项目不能编辑
        if (param.midname) {
            props.form.setFormItemsValue(moduleId, {
                'srcbilltype': { value: null, display: null },
                'srcbillitempk': { value: null, display: null },
                'srcbillitemtxt': { value: null, display: null }
            });
            if (param.midname == IVo2VoItf.HPFC_DEPT) {
                props.form.setFormItemsDisabled(AREA.cardFormId, {
                    'srcbilltype': false,
                    'srcbillitempk': false,
                    'srcbillitemtxt': false
                });
            } else {
                props.form.setFormItemsDisabled(AREA.cardFormId, {
                    'srcbilltype': true,
                    'srcbillitempk': true,
                    'srcbillitemtxt': true
                });
            }
        }

    } else {
        let irows = props.cardTable.getNumberOfRows(tableId);
        for (let i = irows; i >= 0; i--) {
            props.cardTable.delRowsByIndex(tableId, i);
        }
    }
    //==
    chargeDetailShowCol.call(this, props, AREA.cardBody_1, pk_mid_ref.value, idissys.value);
}

function billitempkAfterEdit(props, moduleId, value, oldValue) {
    let tableId = AREA.cardBody_1;
    let [pk_org, idissys, pk_mid_ref, srcbilltype, srcbillitempk] = props.form.getFormItemsValue(moduleId, ["pk_org", "idissys", "pk_mid_ref", "srcbilltype", "srcbillitempk"]);//目标系统
    if (pk_mid_ref.value) {
        //1、清空表体数据
        // props.cardTable.setStatus(tableId, 'browse');


        //2、根据不同参照设置不同的值
        let param = {
            pk_org: pk_org.value,
            pk_group: getBusinessInfo().groupId,
            idissys: idissys.value,
            midpk: pk_mid_ref.value,
            midname: pk_mid_ref.display,
            srcbilltyperefcode: srcbilltype.value,
            srcbillitempk: srcbillitempk.value,
        };
        let irows = props.cardTable.getNumberOfRows(tableId);
        if (irows > 0) {
            MessageDialog.showConfirmDialog("提示", "变更单据项目将清空表体所有行,是否继续？", {
                beSureBtnClick: () => {
                    for (let i = irows; i >= 0; i--) {
                        props.cardTable.delRowsByIndex(tableId, i);
                    }
                    let delrows = props.cardTable.getAllData(tableId).rows;
                    addLine.call(this, props, param, delrows);
                    srcbillitempkAfterEvent.call(this, props, moduleId, value, oldValue);
                },
                cancelBtnClick: () => {
                    props.form.setFormItemsValue(moduleId, {
                        'srcbillitempk': { value: oldValue.value, display: oldValue.display }
                    });
                }
            });
        } else {
            let delrows = props.cardTable.getAllData(tableId).rows;
            addLine.call(this, props, param, delrows);
            srcbillitempkAfterEvent.call(this, props, moduleId, value, oldValue);
        }
    }


}

function addLine(props, param, delrows) {
    //2、根据不同参照设置不同的值
    ajax({
        url: URL.queryBodyRows,
        data: param,
        async: false,
        success: (res) => {
            //填充数据
            if (res.success) {
                let newrow = res.data.bodys[AREA.cardBody_1] ? res.data.bodys[AREA.cardBody_1].rows : [];
                //需要把statue=3的数据拿过来放在表体行里面，不然后端不维护删除状态
                if (delrows && delrows.length > 0) {
                    newrow = newrow.concat(delrows);
                }
                props.cardTable.setTableData(AREA.cardBody_1, { rows: newrow });
            }
        }
    });
    let allrows = props.cardTable.getNumberOfRows(AREA.cardBody_1);
    let rownos = [];
    for (let i = 0; i < allrows; i++) {
        rownos.push(i);
    }
    props.cardTable.setStatus(AREA.cardBody_1, 'edit');
    props.cardTable.setEditableByIndex(AREA.cardBody_1, rownos, ['mcode', 'mname', 'mname1', 'msyscode'], false);
}

function cleanPk_hrp_refItem(props, moduleId, key, value, oldValue) {
    if (key == FIELDS.idissys && !value.value) {//目标系统
        props.form.setFormItemsValue(moduleId, { 'pk_hrp_ref': { value: '', display: '' } });
    }

    if (key == FIELDS.pk_mid_ref && !value.value) {//来源档案类型
        props.form.setFormItemsValue(moduleId, { 'pk_hrp_ref': { value: '', display: '' } });
    }
}

function chargeDetailShowCol(props, tableId, pk_mid_ref, idissys) {
    let param = {
        pk_mid_ref,
        idissys,
    };
    ajax({
        url: URL.queryShowCol,
        data: param,
        async: false,
        success: (res) => {
            let { success, data } = res;
            //填充数据
            if (success) {
                if (data.isShow) {
                    props.cardTable.showColByKey(tableId, data.cols);
                } else {
                    props.cardTable.hideColByKey(tableId, data.cols);
                }
                if (data.appshowcols) {
                    props.cardTable.showColByKey(tableId, data.appshowcols);
                }
            }
        }
    });
}
