
import { PAGECODE, AREA, UISTATUS, BUTTONID, FIELDS, CARDTABLEAREAIDS, URL } from '../../constance';
import pageInfoClick from '../btnClicks/pageInfoClick';
import addBtnClick from '../btnClicks/addBtnClick';
import copyBtnClick from '../btnClicks/copyBtnClick';
import { getAreaAndBtnId } from '../utils/cardPageUtil';
import { lineBtnClick } from '../btnClicks';
import { getLangByResId } from '../../tool/multiLangUtil';
import { ajax, getBusinessInfo } from 'nc-lightapp-front';

export default function (props) {
    props.createUIDom(
        {
            pagecode: PAGECODE.cardPagecode
        },
        (data) => {
            if (data) {
                if (data.context) {
                    this.contexts = data.context;
                }
                if (data.button) {
                    let button = data.button;
                    props.button.hideButtonsByAreas([AREA.cardHead]); // 隐藏所有按钮
                    props.button.setOprationBtnsRenderStatus([AREA.cardBody_1_inner], false);
                    // props.cardTable.selectAllRows(AREA.cardTableId, false); // 取消浏览态表体勾选
                    props.button.setButtons(button);
                    this.props.button.setUploadConfig(BUTTONID.EditImport, this.config);
                }
                if (data.template) {
                    let meta = data.template;
                    modifierMeta.call(this, props, meta);
                    props.meta.setMeta(meta, () => {
                        // 设置默认数据
                        setDefaultData.call(this, props);
                    });
                }
            }
        }
    );
}

function modifierMeta(props, meta) {
    let status = props.getUrlParam('status');
    meta[AREA.cardFormId].status = status;

    let id = props.getUrlParam('id');
    let idissys = props.getUrlParam('idissys');
    let pk_mid_ref = props.getUrlParam('pk_mid_ref');
    let pk_hrp_ref = props.getUrlParam('pk_hrp_ref');
    let showcols = [];
    let hidecols = [];
    let namemap = {};
    let vdefshowlist = [];
    if (pk_mid_ref && pk_hrp_ref) {
        ajax({
            url: URL.initColsShow,
            data: {
                id,
                idissys,
                pk_mid_ref,
                pk_hrp_ref,
            },
            async: false,
            success: (res) => {
                //填充数据
                if (res.success) {
                    showcols = res.data.showList;
                    hidecols = res.data.hideList;
                    namemap = Object.assign(res.data.nameMap);
                    vdefshowlist = res.data.vdefShowList;
                }
            }
        });
    }


    let map = new Map();
    let fieldcodes = [];
    if (vdefshowlist && vdefshowlist.length > 0) {
        fieldcodes = vdefshowlist.map(item => {
            map.set(item.fieldcode, item.attrcode);
            return item.fieldcode;
        });
    }

    [AREA.cardBody_1].forEach((moduleId) => {
        meta[moduleId].status = status;
        //设置动态列展示 suqc
        meta[moduleId].items.map((item, key) => {
            if (showcols.length > 0 && showcols.indexOf(item.attrcode) != -1) {
                item.visible = true;
            }

            if (fieldcodes.indexOf(item.attrcode) > -1) {
                item.visible = true;
                item.attrname = namemap[map.get(item.attrcode)];
                item.label = namemap[map.get(item.attrcode)];
            }

            if (hidecols.length > 0 && hidecols.indexOf(item.attrcode) != -1) {
                item.visible = false;
            }
            return item;
        });

        // 操作列
        meta[moduleId].items.push({
            label: getLangByResId(this, '4001PUBMESSAGE-0000006') /* 国际化处理： 操作*/,
            itemtype: 'customer',
            attrcode: 'opr',
            width: '160px',
            visible: true,
            fixed: 'right',
            render: (text, record, index) => {
                if (props.getUrlParam('status') != UISTATUS.browse) {
                    let { InnerAreaId, InnerBtnIds } = getAreaAndBtnId(
                        moduleId,
                        props.getUrlParam('status'),
                        this.copyLineDatas[moduleId]
                    );

                    return props.button.createOprationButton(InnerBtnIds, {
                        area: InnerAreaId,
                        buttonLimit: 3,
                        onButtonClick: (props, key) =>
                            lineBtnClick.call(this, moduleId, props, key, text, record, index)
                    });
                }
            }
        });
    });
    //搜索参照过滤
    meta[AREA.cardFormId].items.map((item) => {
        if (item.attrcode === 'pk_org') { //财务组织过滤
            item.queryCondition = () => ({
                funcode: props.getSearchParam('c'),//appcode获取
                //用户权限过滤
                TreeRefActionExt: 'nccloud.web.refer.sqlbuilder.PrimaryOrgSQLBuilder'
            });
        }
    });
    return meta;
}


function setDefaultData(props) {
    let status = props.getUrlParam('status');
    let pk = props.getUrlParam('id');
    let idissys = props.getUrlParam('idissys');
    let pk_mid_ref = props.getUrlParam('pk_mid_ref');
    let pk_hrp_ref = props.getUrlParam('pk_hrp_ref');
    let param = {
        idissys,
        pk_mid_ref,
        pk_hrp_ref
    };
    if (status == UISTATUS.add) {
        addBtnClick.call(this, props);
    } else if (status == UISTATUS.copy) {
        copyBtnClick.call(this, props, pk);
    } else {
        pageInfoClick.call(this, props, pk, false, param);
    }

    // 设置按钮可用性
    props.button.setButtonDisabled(
        [
            BUTTONID.cardBody_1_DeleteLine,
            BUTTONID.cardBody_1_CopyLine
        ],
        true
    );
}
