
import { ajax } from 'nc-lightapp-front';
import { AREA, URL, FIELDS, UISTATUS } from '../../constance';
import { setCardPage } from '../utils/cardPageUtil';

export default function(props, pk) {
        pk = pk ? pk : props.form.getFormItemsValue(AREA.cardFormId, FIELDS.pk_head_fields).value;
        ajax({
                url: URL.copy,
                data: pk,
                success: (res) => {
                        if (res.success && res.data) {
                                props.setUrlParam({
                                        status: UISTATUS.copy,
                                        id: pk
                                });

                                setCardPage.call(this, props, res.data, false);
                        }
                }
        });
}
