
import { BUTTONID, AREA } from '../../constance';
import backBtnClick from './backBtnClick';
import addBtnClick from './addBtnClick';
import editBtnClick from './editBtnClick';
import refreshBtnClick from './refreshBtnClick';
import deleteBtnClick from './deleteBtnClick';
import cancelBtnClick from './cancelBtnClick';
import copyBtnClick from './copyBtnClick';
import saveBtnClick from './saveBtnClick';
import addLineBtnClick from './addLineBtnClick';
import deleteLineBtnClick from './deleteLineBtnClick';
import copyLineBtnClick from './copyLineBtnClick';
import pasteLineBtnClick from './pasteLineBtnClick';
import cancelBBtnClick from './cancelBBtnClick';
import insertLineBtnClick from './insertLineBtnClick';
import automaticControlClick from './automaticControlClick';
import editExportClick from './editExportClick';

// 表头按钮区操作
function buttonClick(props, id) {
    switch (id) {
        case BUTTONID.Add:
            addBtnClick.call(this, props);
            break;
        case BUTTONID.Edit:
            editBtnClick.call(this, props);
            break;
        case BUTTONID.Delete:
            deleteBtnClick.call(this, props);
            break;
        case BUTTONID.Copy:
            copyBtnClick.call(this, props);
            break;
        case BUTTONID.Refresh:
            refreshBtnClick.call(this, props);
            break;
        case BUTTONID.Save:
            saveBtnClick.call(this, props);
            break;
        case BUTTONID.Cancel:
            cancelBtnClick.call(this, props);
            break;
        case BUTTONID.Back:
            backBtnClick.call(this, props);
            break;
        case BUTTONID.AutomaticControl:
            automaticControlClick.call(this, props);
            break;
        case BUTTONID.EditExport:
            editExportClick.call(this, props);
            break;
        case BUTTONID.EditImport:
            let flag = props.form.isCheckNow(AREA.cardFormId);
            if (!flag) return;

            let headcols = props.meta.getMeta()[AREA.cardFormId].items.map(item => [item.attrcode, item.label]);
            let showhideflag = props.form.getFormItemsVisible(AREA.cardFormId, headcols.map(item => item[0]));
            let headinfo = new Map();
            //获取表头显示的字段以及值
            headcols.forEach((item, index) => {
                if (showhideflag[index]) {
                    headinfo.set(item, props.form.getFormItemsValue(AREA.cardFormId, item[0]));
                }
            });
            //获取表体显示的字段信息
            let bodyinfo = props.meta.getMeta()[AREA.cardBody_1].items.filter(item => item.visible && item.attrcode !== 'rowno' && item.attrcode !== 'opr').map(item => [item.attrcode, item.label]);
            let bodys = props.cardTable.getVisibleRows("cardBody_1");
            let bodydatas = [];
            bodys.forEach(item => {
                let bodydata = {};
                bodydata.rowno = item.values.rowno.value;
                bodydata.hpk = item.values.hpk.value;
                bodydata.hcode = item.values.hcode.value;
                bodydata.hname = item.values.hname.value;
                bodydata.msyscode = item.values.msyscode.value;
                bodydata.mpk = item.values.mpk.value;
                bodydata.mcode = item.values.mcode.value;
                bodydata.mname = item.values.mname.value;
                bodydatas.push(bodydata);
            });
            this.config.data = {
                headinfo: JSON.stringify(headinfo),
                bodyinfo: JSON.stringify(bodyinfo),
                bodydata: JSON.stringify(bodydatas)
            };
            this.props.button.setUploadConfig(BUTTONID.EditImport, this.config);
            break;
    }
}

// 表体按钮操作
function lineBtnClick(moduleId, props, id, text, record, index) {
    switch (id) {
        case BUTTONID.cardBody_1_AddLine:
            addLineBtnClick.call(this, props, moduleId);
            break;

        case BUTTONID.cardBody_1_DeleteLine:
            deleteLineBtnClick.call(this, props, moduleId, record, index);
            break;
        case BUTTONID.cardBody_1_CopyLine:
            copyLineBtnClick.call(this, props, moduleId);
            break;

        case BUTTONID.cardBody_1_PasteLineToTail:
        case BUTTONID.cardBody_1_PasteLine:
            pasteLineBtnClick.call(this, props, moduleId, record, index);
            break;

        case BUTTONID.cardBody_1_CancelB:
            cancelBBtnClick.call(this, props, moduleId);
            break;

        case BUTTONID.cardBody_1_InsertLine:
            insertLineBtnClick.call(this, props, moduleId, record, index);
            break;
    }
}

export { buttonClick, lineBtnClick };
