import { ajax, promptBox, toast, getBusinessInfo } from 'nc-lightapp-front';
import { AREA, URL, PAGECODE, CARDTABLEAREAIDS, IVo2VoItf } from '../../constance';

export default function (props) {

        // 表头必输项校验
        let flag = props.form.isCheckNow(AREA.cardFormId);
        if (!flag) return;

        let data = props.createExtCardData(PAGECODE.cardPagecode, AREA.cardFormId, CARDTABLEAREAIDS);
        data.head.card_head.rows[0].values.pk_group = { display: getBusinessInfo().groupName, value: getBusinessInfo().groupId };
        if(data.head.card_head.rows[0].values.dr){
                //卡片页面点新增会报dr=null的问题,修复
                delete data.head.card_head.rows[0].values.dr;
        }
        ajax({
                url: URL.automaticControl,
                data: data,
                async: false,
                success: (res) => {
                        if (res.success && res.data) {
                                let children = res.data.children[0];
                                for (let i = 0; i < children.length; i++) {
                                        props.cardTable.setValByKeyAndIndex(AREA.cardBody_1, i, IVo2VoItf.KEY_HCODE, { value: children[i].hcode, display: children[i].hcode });
                                        props.cardTable.setValByKeyAndIndex(AREA.cardBody_1, i, "hname1", { value: children[i].hcode, display: children[i].hcode });
                                        props.cardTable.setValByKeyAndIndex(AREA.cardBody_1, i, IVo2VoItf.KEY_HNAME, { value: children[i].hname, display: children[i].hname });
                                        props.cardTable.setValByKeyAndIndex(AREA.cardBody_1, i, IVo2VoItf.KEY_HPK, { value: children[i].hpk, display: children[i].hpk });
        
                                }
                        }
                }
        });
}