
import { FIELDS, BUTTONID, AREA, URL, IVo2VoItf,DisSysEnum } from '../../constance';
import { ajax, getBusinessInfo } from 'nc-lightapp-front';
import * as MessageDialog from '../../tool/messageUtil'

export default async function (props, moduleId, key, value, index, record) {
        return new Promise(function (resolve, reject){
                let [pk_org, idissys, pk_mid_ref, pk_hrp_ref,srcbilltype, srcbillitempk, pk_interdocref] = props.form.getFormItemsValue(AREA.cardFormId, [FIELDS.pk_org, FIELDS.idissys, "pk_mid_ref", "pk_hrp_ref","srcbilltype","srcbillitempk","pk_interdocref"]);//目标系统
                if (!pk_mid_ref.value || !pk_hrp_ref.value) {
                        MessageDialog.showWarningInfo("请注意", "请先完善表头信息！");
                        return reject(false);
                }
                if (key === 'mname1') {
                        let data = beforeEditForMID.call(this, props, key);
                        let {refvo, ismulti} = data;
                        let meta = props.meta.getMeta();
                        meta[moduleId].items.map((item) => {
                                if (item.attrcode == 'mname1') {
                                        item.isMultiSelectedEnabled = ismulti ? true : false;
                                        if(refvo.refpath){
                                                item.refcode = refvo.refpath;
                                                item.queryCondition = {
                                                        pk_org: pk_org.value,
                                                        pk_group: getBusinessInfo().groupId,
                                                        pk_interdocref: pk_interdocref.value,
                                                        idissys: idissys.value,
                                                        midpk: pk_mid_ref.value,
                                                        midname: pk_mid_ref.display,
                                                        hrppk: pk_hrp_ref.value,
                                                        hrpname: pk_hrp_ref.display,
                                                        srcbilltypepk: srcbilltype.value,
                                                        srcbillitempk: srcbillitempk.value,
                                                        srcbilltypename: srcbilltype.display,
                                                        GridRefActionExt: 'nccloud.web.hpf.itfdocref.refsqlbuilder.Mname1RefSqlBuilder'
                                                }
                                        } else {
                                                //TODO 后续处理
                                                MessageDialog.showInfoDialog("提示","未找到动态参照refpath,请注册!"); 
                                        }
                                }
                        });
                        props.meta.setMeta(meta, () => {
                                resolve(true);
                        });
                }else if(key === 'hname1'){
                        let data = beforeEditForHRP(props, key, index);
                        let {refvo, ismulti, isLeafsel, wherePart, pk_accbook} = data;
                        let meta = props.meta.getMeta();
                        meta[moduleId].items.map((item) => {
                                if (item.attrcode == 'hname1') {
                                        if(ismulti){
                                                item.isMultiSelectedEnabled = ismulti ? true : false;
                                        } 
                                        if(isLeafsel){
                                                //TODO 设置参照是否只能选子节点
                                                item.onlyLeafCanSelect = isLeafsel ? true : false;
                                        }
                                        if(refvo.refpath){
                                                item.refcode = refvo.refpath;
                                                item.queryCondition = () => {
                                                        let condition = {                                                        
                                                                pk_org: pk_org.value,
                                                                pk_group: getBusinessInfo().groupId,
                                                                pk_interdocref: pk_interdocref.value,
                                                                idissys: idissys.value,
                                                                midpk: pk_mid_ref.value,
                                                                midname: pk_mid_ref.display,
                                                                hrppk: pk_hrp_ref.value,
                                                                hrpname: pk_hrp_ref.display,
                                                                wherePart: wherePart,
                                                                GridRefActionExt: 'nccloud.web.hpf.itfdocref.refsqlbuilder.Hname1RefSqlBuilder'
                                                        };
                                                        
                                                        if(pk_accbook){
                                                                condition.pk_accountingbook = pk_accbook.value;
                                                        }

                                                        if(refvo.para1){
                                                                condition.pk_defdoclist = refvo.para1;
                                                        }

                                                        return condition
                                                }
                                               

                                        }
                                }
                        });
                        props.meta.setMeta(meta, () => {
                                resolve(true);
                        });
                        
                }
                else if(key === "pk_measdoc"){
                        let meta = props.meta.getMeta();
                        let hpk = props.cardTable.getValByKeyAndIndex(AREA.cardBody_1, index,'hpk');
                        let pk_material = hpk.value;
                        if(pk_material){
                                meta[moduleId].items.map((item) => {
                                        if (item.attrcode == 'pk_measdoc') {
                                             items.queryCondition = {
                                                wherePart: `  and pk_measdoc in (select pk_measdoc from bd_materialconvert 
                                                 where pk_material = '${pk_material}' and dr = 0 "
                                                 union
                                                 select pk_measdoc from bd_material where pk_material = '${pk_material}')`
                                             };
                                        }
                                });
                                props.meta.setMeta(meta, () => {
                                        resolve(true);
                                });
                        }
                }else if(key === "stordoccode"){
                        //     new StoreHandler().beforeEdit(e);
                        let meta = props.meta.getMeta();
                        meta[moduleId].items.map((item) => {
                                if (item.attrcode == 'stordoccode') {
                                     items.queryCondition = {
                                        partWhere: ` and csflag = 'Y' `,
                                     };
                                }
                        });
                        props.meta.setMeta(meta, () => {
                                resolve(true);
                        });
                }

                resolve(true);
        });

}

function beforeEditForHRP(props, key, index){
        let retobj = {};
        let [pk_org,idissys, pk_mid_ref, pk_hrp_ref] = props.form.getFormItemsValue(AREA.cardFormId, ["pk_org","idissys", "pk_mid_ref", "pk_hrp_ref"]);//目标系统
        let param = {
                key: key,
                idissys: idissys.value,
                pk_mid_ref: pk_mid_ref.value,
                midname: pk_mid_ref.display,
                pk_hrp_ref: pk_hrp_ref.value,
                pk_org: pk_org.value,
        };
        let pk_storedoc = props.cardTable.getValByKeyAndIndex(AREA.cardBody_1, index,'stordoccode').value;
        if(pk_storedoc){
                param.pk_storedoc = pk_storedoc;
        }
        ajax({
                url: URL.dynamicRefHandle,
                data: param,
                mode: 'post',
                async: false,
                success: (res) => {
                        if (res.success && res.data != null) {
                               retobj = res.data;
                        }
                }
        });
        return retobj;  
}

function beforeEditForMID(props, key) {
        let retobj = {};
        let [idissys, pk_mid_ref, pk_hrp_ref] = props.form.getFormItemsValue(AREA.cardFormId, ["idissys", "pk_mid_ref", "pk_hrp_ref"]);//目标系统
        let param = {
                key: key,
                idissys: idissys.value,
                pk_mid_ref: pk_mid_ref.value,
                midname: pk_mid_ref.display,
                pk_hrp_ref: pk_hrp_ref.value,
        };
        ajax({
                url: URL.dynamicRefHandle,
                data: param,
                mode: 'post',
                async: false,
                success: (res) => {
                        if (res.success && res.data != null) {
                               retobj = res.data;
                        }
                }
        });
        return retobj;
}
