
import { UISTATUS, CARDTABLEAREAIDS, BUTTONID } from '../../constance';
import { buttonControl } from '../viewControl/buttonControl';

export default function(props) {
        props.setUrlParam({ status: UISTATUS.edit });
        CARDTABLEAREAIDS.forEach((areaId) => {
                props.cardTable.selectAllRows(areaId, false); // 取消浏览态表体勾选
        });
        buttonControl.call(this, props);
}
